.container {
  width: 100%;
  padding: 2rem;
  box-sizing: border-box;
}

.contentRow {
  display: flex;
  gap: 2rem;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap; /* Allow stacking on small screens */
}

.imageCol {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  position: relative; /* Allow children to be offset */
}


.image {
  width: 100%;
  max-width: 500px;
  height: auto;
}

.textCol {
  flex: 1;
  color: white;
  max-width: 600px;
}

.title {
    font-size: 1.8rem;
    color: #ffffff;
    font-weight: 150;
    margin: 0;
    text-align: justify;
    align-self: flex-start;
}

.detail {
    font-weight: 120;
    font-size: 0.8rem;
    margin: 0;
    padding: 0;
    text-align: justify;
    align-self: flex-start;
}

.description {
    font-size: 1rem;
    font-weight: 200;
    line-height: 1.3;
    color: #ffffff;
    padding: 0px;
    margin: 0;
    text-align: justify;
    align-self: flex-start;
}

/* Responsive behavior */
@media (max-width: 768px) {
  .contentRow {
    flex-direction: column;
    align-items: center;
  }

  .textCol {
    max-width: 100%;
  }

  .imageCol {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .image {
    max-width: 45%;
    margin: 0.5rem;
  }
}
