.frontPage {
  height: 100vh;
  overflow: visible; /* Allow overflow */
  position: relative;
  background-color: #134e75;
  z-index: 0; /* Or positive number */
}


.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
}

.waveLine {
  position: absolute;
  width: 100%;
  height: 100px;
}

.waveLine:nth-of-type(1) { top: 0%; }
.waveLine:nth-of-type(2) { top: 75%; }
.waveLine:nth-of-type(5) { top: 100%; }

.contentWrapper {
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}

.section {
  min-height: 100vh;
  scroll-snap-align: start;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}


/* FrontPage.module.css */
.frontPageHeader {
  font-size: 3em; /* Larger font size */
  text-align: center; /* Center the text */
  color: #ffffff; /* Change the color if needed */
  font-family: "Grandiflora One", cursive;
  font-weight: 400;
  font-style: normal;
  top: 100%;
}

.frontPageLinks {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  grid-template-rows: repeat(2, auto); /* 2 rows */
  gap: 20px; /* Adjust spacing between items */
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  max-width: 900px; /* Adjust as needed */
}

.frontPageLinks a {
  display: block;
  text-align: center;
}

.frontPageLinks img {
  height: 200px;
  width: auto;
  object-fit: cover;
  filter: grayscale();
}

.frontPageLinks img:hover {
  filter: grayscale(0);
}


/* Tooltip styling */
.tooltip {
  position: absolute;
  z-index: 999;
  /* background-color: rgb(68, 52, 4); /* Slightly darker background */
  background: transparent;
  color: rgb(255, 255, 255);
  padding: 0px 6px 0px 6px;
  width: 240px; /* Adjust width as needed */
  font-family: Arial, sans-serif; /* Optional: Set font */
}

/* Tooltip text style */
.tooltip p {
  margin: 2px 10px;
  padding: 2px;
  font-size: 14px;
}

.tooltip p strong {
  font-size: 16px; /* Highlight the main project and artist */
  color: #ffffff;
}

.tooltip p:first-child {
  font-size: 18px;
  font-weight: bold;
}

.tooltip p:last-child {
  border-bottom: none; /* Remove line from the last paragraph */
}

.visualDiv {
  padding-top: 120px;
}

.visualDiv h1::before {
  content: ":"; 
  visibility: hidden; /* Keeps space but hides colon */
}

.visualDiv h1:hover::before {
  visibility: visible; /* Makes colon appear */
}

.fadeIn {
  opacity: 0;
  animation: fadeInEffect 1.5s ease-in forwards;
}

@media (max-width: 768px) {
  .frontPage {
    flex-direction: column;
    height: auto; /* Allow scrolling */
  }

  .videoBackground {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  .contentWrapper {
    width: 90%; /* Take more space on smaller screens */
    margin: 20px auto; /* Center content */
    height: auto;
    overflow: visible;
    padding: 10px;
  }

  .frontPageHeader {
    font-size: 2em; /* Reduce font size */
  }

  .frontPageLinks {
    grid-template-columns: 1fr; /* Stack links in one column */
    grid-template-rows: auto;
    gap: 15px;
  }

  .frontPageLinks img {
    height: 150px; /* Reduce image size */
    width: 100%; /* Make images fill container */
  }

  .tooltip {
    width: 180px; /* Adjust tooltip size */
    font-size: 12px;
  }

  .tooltip p {
    font-size: 12px;
  }

  .tooltip p strong {
    font-size: 14px;
  }

  .visualDiv {
    padding-top: 60px;
  }

  .visualDiv h1 {
    font-size: 1.5em; /* Adjust heading size */
  }
}

@media (max-width: 480px) {
  .frontPageHeader {
    font-size: 1.8em;
  }

  .frontPageLinks img {
    height: 120px; /* Further reduce image size */
  }

  .tooltip {
    width: 150px;
  }

  .tooltip p {
    font-size: 10px;
  }
}


@keyframes fadeInEffect {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
